<template>
  <div class="dashboard-container">
    <div class="app-container">
      <el-card>
        <div slot="header">{{ $route.query.id ? "" : "" }}</div>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="120px"
          class="demo-ruleForm"
        >
          <!--  -->
          <el-form-item label="" prop="subjectID">
            <el-select
              @change="changeSubject"
              v-model="ruleForm.subjectID"
              placeholder=""
              style="width: 400px"
            >
              <el-option
                v-for="item in subjectOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="catalogID">
            <el-select
              v-model="ruleForm.catalogID"
              placeholder=""
              style="width: 400px"
            >
              <el-option
                v-for="item in catalogOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="enterpriseID">
            <el-select
              v-model="ruleForm.enterpriseID"
              placeholder=""
              style="width: 400px"
            >
              <el-option
                v-for="item in enterpriseOptions"
                :key="item.id"
                :label="item.company"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="city">
            <el-select
              v-model="ruleForm.province"
              placeholder=""
              @change="getCity"
              style="width: 198px"
            >
              <el-option
                v-for="item in cityOptions"
                :key="item"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>

            <el-select
              v-model="ruleForm.city"
              placeholder=""
              style="width: 198px; margin-left: 4px"
            >
              <el-option
                v-for="item in areaOptions"
                :key="item"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="direction">
            <el-select
              v-model="ruleForm.direction"
              placeholder=""
              style="width: 400px"
            >
              <el-option
                v-for="item in directionOptions"
                :key="item"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="questionType">
            <el-radio-group
              v-model="ruleForm.questionType"
              @change="changeRadio({})"
            >
              <el-radio
                v-for="item in questionType"
                :key="item.value"
                :label="item.value + ''"
                >{{ item.label }}</el-radio
              >
            </el-radio-group>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="difficulty">
            <el-radio-group v-model="ruleForm.difficulty">
              <el-radio
                v-for="item in difficulty"
                :key="item.value"
                :label="item.value + ''"
                >{{ item.label }}</el-radio
              >
            </el-radio-group>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="question">
            <quill-editor
              v-model="ruleForm.question"
              ref="myQuillEditor"
              :options="editorOptions"
              @blur='checkQustion'
            ></quill-editor>
          </el-form-item>

          <!--  -->
          <el-form-item
            label=""
            prop="options"
            v-if="ruleForm.questionType !== '3'"
          >
            <div
              class="option_item"
              v-for="item in ruleForm.options"
              :key="item.code"
            >
              <el-radio
                v-if="ruleForm.questionType === '1'"
                @change="changeRadio(item)"
                v-model="item.isRight"
                style="margin-right: 0"
                :label="true"
                >{{ item.code }}</el-radio
              >
              <el-checkbox
                v-if="ruleForm.questionType === '2'"
                v-model="item.isRight"
                style="margin-right: 0"
                :label="true"
                >{{ item.code }}</el-checkbox
              >
              <el-input v-model="item.title" style="width: 240px"></el-input>
              <el-upload
                style="margin-left: 4px"
                action="https://jsonplaceholder.typicode.com/posts/"
                class="avatar-uploader"
                :on-success="(res, file) => {
                  handleAvatarSuccess(item, res, file);
                }"
                :show-file-list="false"
              >
                <img v-if="item.img" :src="item.img" class="avatar" />
                <span v-else></span>
                <i class="el-icon-circle-close" />
              </el-upload>
            </div>

            <!--  -->
            <el-button
              type="danger"
              :disabled="ruleForm.questionType !== '2'"
              @click="addOption"
              size="small"
              >+</el-button
            >
          </el-form-item>

          <!--  -->
          <el-form-item label="">
            <el-input
              v-model="ruleForm.videoURL"
              style="width: 400px"
            ></el-input>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="answer">
            <quill-editor
              v-model="ruleForm.answer"
              ref="myQuillEditor"
              :options="editorOptions"
              @blur='checkAnswer'
            ></quill-editor>
          </el-form-item>

          <!--  -->
          <el-form-item label="">
            <el-input
              type="textarea"
              v-model="ruleForm.remarks"
              :rows="4"
              style="width: 400px"
            ></el-input>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="tags">
            <el-select
              v-model="ruleForm.tags"
              multiple
              filterable
              allow-create
              default-first-option
              placeholder=""
              style="width: 400px"
            >
              <el-option
                v-for="item in tagsOptions"
                :key="item.value"
                :label="item.label"
                :value="item.label"
              >
              </el-option>
            </el-select>
          </el-form-item>

          <!--  -->
          <el-form-item>
            <el-button type="primary" v-if="!$route.query.id" @click="submit()"
              ></el-button
            >
            <el-button v-else @click="revise()" type="success"
            ></el-button
          >
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
import { simple as getSubjectOptions } from '@/api/hmmm/subjects'
import { simple as getCatalogOptions } from '@/api/hmmm/directorys'
import { list as getCompanyOptions } from '@/api/hmmm/companys'
import { simple as getTagOptions } from '@/api/hmmm/tags'
import {
  provinces as getCityOptions,
  citys as getAreaOptions
} from '@/api/hmmm/citys'
import { direction, questionType, difficulty } from '@/api/hmmm/constants'
import { add, detail, update } from '@/api/hmmm/questions'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import hljs from 'highlight.js'
import 'highlight.js/styles/vs2015.css'
export default {
  name: 'questions-new',
  components: {
    quillEditor
  },
  data () {
    return {
      // img: '',
      editorOptions: {
        placeholder: '',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            [{ list: 'ordered' }, { list: 'bullet' }],
            ['blockquote'],
            ['code-block', 'image', 'link']
          ],
          // 
          syntax: {
            highlight: (text) => hljs.highlightAuto(text).value
          }
        }
      },
      ruleForm: {
        // 
        subjectID: null,
        // 
        catalogID: null,
        // 
        enterpriseID: null,
        // 
        province: null,
        // 
        city: null,
        // 
        direction: null,
        // 
        questionType: '1',
        // 
        difficulty: '1',
        // 
        question: null,
        // 
        answer: null,
        tags: null,
        // 
        options: [
          { isRight: false, code: 'A', title: '', img: '' },
          { isRight: false, code: 'B', title: '', img: '' },
          { isRight: false, code: 'C', title: '', img: '' },
          { isRight: false, code: 'D', title: '', img: '' }
        ]
      },
      rules: {
        // 
        subjectID: [
          { required: true, message: '', trigger: 'change' }
        ],
        // 
        catalogID: [
          { required: true, message: '', trigger: 'change' }
        ],
        // 
        enterpriseID: [
          { required: true, message: '', trigger: 'change' }
        ],
        // 
        city: [{ required: true, message: '', trigger: 'change' }],
        // 
        direction: [
          { required: true, message: '', trigger: 'change' }
        ],
        // 
        questionType: [
          { required: true, message: '', trigger: 'change' }
        ],
        // 
        difficulty: [
          { required: true, message: '', trigger: 'change' }
        ],
        // 
        question: [
          { required: true, message: '', trigger: 'blur' }
        ],
        // 
        answer: [
          { required: true, message: '', trigger: 'blur' }
        ],
        tags: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      subjectOptions: [],
      // 
      catalogOptions: [],
      // 
      enterpriseOptions: [],
      // 
      tagsOptions: [],
      // 
      cityOptions: getCityOptions(),
      // 
      areaOptions: [],
      tags: [],
      // 
      directionOptions: direction,
      // 
      questionType,
      // 
      difficulty
    }
  },
  created () {
    this.getSubjectOptions()
    this.getCompanyOptions()
    if (this.$route.query.id) {
      this.getQuestion()
    }
  },
  watch: {
    '$route.query': function () {
      if (this.$route.query.id) {
        this.getQuestion()
      } else {
        this.ruleForm = {
          subjectID: null,
          catalogID: null,
          enterpriseID: null,
          province: null,
          city: null,
          direction: null,
          questionType: '1',
          difficulty: '1',
          question: null,
          answer: null,
          tags: null,
          options: [
            { isRight: false, code: 'A', title: '', img: '' },
            { isRight: false, code: 'B', title: '', img: '' },
            { isRight: false, code: 'C', title: '', img: '' },
            { isRight: false, code: 'D', title: '', img: '' }
          ]
        }
        this.$nextTick(() => {
          this.$refs.form.clearValidate()
        })
      }
    }
  },
  methods: {
    // 
    async getQuestion () {
      const { data } = await detail({ id: this.$route.query.id })
      data.tags = data.tags.split(',')
      data.options = data.options.map((item) => {
        item.isRight = item.isRight === 1
        return item
      })
      this.ruleForm = data
      const res = await getCatalogOptions({
        subjectID: this.ruleForm.subjectID
      })
      this.catalogOptions = res.data
      const res2 = await getTagOptions({
        subjectID: this.ruleForm.subjectID
      })
      this.tagsOptions = res2.data
    },

    // 
    async changeSubject (subjectID) {
      this.ruleForm.catalogID = null
      const res = await getCatalogOptions({
        subjectID: this.ruleForm.subjectID
      })
      this.catalogOptions = res.data
      this.ruleForm.tags = null
      const res2 = await getTagOptions({ subjectID: this.ruleForm.subjectID })
      this.tagsOptions = res2.data
    },
    // 
    async getSubjectOptions () {
      const res = await getSubjectOptions()
      this.subjectOptions = res.data
      console.log(res.data)
    },
    // 
    async getCompanyOptions () {
      const res = await getCompanyOptions({ pagesize: 1000 })
      this.enterpriseOptions = res.data.items
    },
    // 
    getCity (city) {
      this.ruleForm.city = null
      this.areaOptions = getAreaOptions(city)
    },
    // 
    checkQustion () {
      this.$refs.ruleForm.validateField('question')
    },
    checkAnswer () {
      this.$refs.ruleForm.validateField('answer')
    },

    handleAvatarSuccess (item, res, file) {
      item.img = URL.createObjectURL(file.raw)
    },

    // 
    // 
    changeRadio (item) {
      this.ruleForm.options.forEach((item) => {
        item.isRight = false
      })
      // 
      item.isRight = true
    },
    // 
    addOption () {
      this.ruleForm.options.push({
        isRight: false,
        code: String.fromCharCode(65 + this.ruleForm.options.length),
        title: '',
        img: ''
      })
    },
    // 
    submit () {
      this.$refs.ruleForm.validate(async (valid) => {
        if (valid) {
          const data = { ...this.ruleForm }
          data.tags = data.tags.join(',')
          await add(data)
          this.$message.success('')
          this.$router.push('/questions/list')
        }
      })
    },
    // 
    revise () {
      this.$refs.ruleForm.validate(async (valid) => {
        if (valid) {
          const data = { ...this.ruleForm }
          data.tags = data.tags.join(',')
          await update(data)
          this.$message.success('')
          this.$router.push('/questions/list')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .ql-editor {
  height: 200px;
}
::v-deep .ql-toolbar.ql-snow {
  padding: 0 8px;
}
